<template>
  <div class="xc-detail">
    <template v-for="(item, index) in sourceData.children">
      <video v-if="getSuffix(item).endsWith('mp4')" :key="index" :src="`./20240212-太姥山/${item}`"></video>
      <img v-else :key="index" :src="`/images/${item}`" width="200" loading="lazy"  alt="" >
    </template>
  </div>
</template>

<script>
export default {
  props: {
    sourceData: {
      type: Object,
      required: true
    }
  },

  methods: {
    getSuffix (name) {
      return name.split('.')[1].toLocaleLowerCase()
    }
  }

}
</script>

